.about {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5vh 16vw;
  background-color: #d2d9e2;
}

.skills {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 35px;
}
.about > h2 {
  font-size: 17px;
  text-align: center;
  font-weight: 400;
}
.skills > h2 {
  padding: 6px;
  font-weight: 400;
  font-size: 17px;
}

.skills > code {
  font-size: 17px;
  font-weight: 550;
  color: #8029d7;
  padding-left: 19px;
}
