#contact > form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
#contact > form > div {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 200px;
  width: 90vw;
  max-width: 370px;
  margin: 10px 0px;
}

form > div > input {
  height: 42px;
  width: 89vw;
  max-width: 300px;
  border-radius: 10px;
  border: none;
  outline: none;
  padding: 10px;
}
form > div > textarea {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  resize: none;
  border: none;
  outline: none;
  padding: 10px;
  font-size: 13px;
}
form > div > button {
  width: 70vw;
  max-width: 250px;
  height: 45px;
  border-radius: 12px;
  background-color: #a978e6;
  border: none;
  outline: none;
  font-size: 16px;
}

@media screen and (max-width: 760px) {
  #contact > form {
    flex-direction: column;
  }
}
